$mt-breakpoints: (
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

:host {
  margin: 0 auto;
  display: block;

  column-count: var(--mt-columns-number);
  column-gap: var(--mt-column-gap);
  max-width: var(--mt-columns-max-width);
  --mt-columns-number: null;
  --mt-columns-max-width: 100%;
  --mt-column-gap: 40px;
}
